@import "core.variables";

.variable-table {
  @include theme {
    border-radius: token('border-radius');
    border: token('border-width') solid token('color-border'); }
  display: table;
  width: 100%;

  &__header {
    display: table-header-group; }

  &__header-item {
    @include theme {
      background: token('color-background-alt'); }
    display: table-cell;
    padding: $space-1 $space-2; } }



