@import "core.variables";

.svg-figure {
  display: inline-flex;
  align-items: center; }

.svg {
  @include theme((fill: color-fill)); }
.svg--inherit {
  @include theme((fill: color-fill-current)); }
.svg--alt {
  @include theme((fill: color-fill-alt)); }
.svg--alt-2 {
  @include theme((fill: color-fill-alt-2)); }
.svg--brand {
  @include theme((fill: color-fill-brand)); }
.svg--live {
  @include theme((fill: color-fill-live)); }
.svg--warn {
  @include theme((fill: color-fill-warn)); }
.svg--alert {
  @include theme((fill: color-fill-alert)); }
.svg--success {
  @include theme((fill: color-fill-success)); }
.svg--prime {
  @include theme((fill: color-fill-prime)); }



