@import 'core.variables';

// Range settings.
$track-height: 0.5rem;
$thumb-height: 2.5rem;
$thumb-width: 1rem;

// The vendor prefixes don't work properly when selected together (via commas),
// so we're using mixins to consolidate the styles while selecting the
// pseudo-elements separately.
@mixin range-slider {
  @include theme {
    background: token('color-border-input');
    box-shadow: token('color-border-input') 0 0 0 token('border-width') inset; }
  width: 100%;
  height: $track-height;
  cursor: pointer; }

@mixin range-thumb {
  @include theme {
    box-shadow: token('color-border-input') 0 0 0 token('border-width') inset; }
  height: $thumb-height;
  width: $thumb-width;
  background: $white;
  margin-top: ($track-height / 2) - ($thumb-height / 2);
  border-radius: 1px;
  border: none;
  appearance: none; }

@mixin range-thumb-focus {
  @include theme {
    box-shadow: token('color-border-input-focus') 0 0 0 token('border-width') inset, token('shadow-input-focus'); } }

@mixin range-lower-upper {
  @include theme {
    background: token('color-border-input');
    box-shadow: token('color-border-input') 0 0 0 token('border-width') inset; } }

.tw-form__input {
  &[type='range'] {
    width: 100%;
    height: $thumb-height;
    border: none;
    background: transparent;
    vertical-align: middle;
    appearance: none;

    // Slider
    // ------------------------------------------------------------------------
    &::-moz-range-track {
      @include range-slider; }

    &::-ms-track {
      @include range-slider; }

    &::-webkit-slider-runnable-track {
      @include range-slider; }

    // Thumb
    // ------------------------------------------------------------------------
    &::-moz-range-thumb {
      @include range-thumb; }

    &::-ms-thumb {
      @include range-thumb; }

    &::-webkit-slider-thumb {
      @include range-thumb; }

    // Lower/Upper
    // ------------------------------------------------------------------------
    &::-ms-fill-lower {
      @include range-lower-upper; }

    &::-ms-fill-upper {
      @include range-lower-upper; }

    // Thumb (focused)
    // ------------------------------------------------------------------------
    &:focus {
      outline: none;

      &::-moz-range-thumb {
        @include range-thumb-focus; }

      &::-ms-thumb {
        @include range-thumb-focus; }

      &::-webkit-slider-thumb {
        @include range-thumb-focus; } } } }



