@import 'core.variables';

.tw-tabs {
  @include theme {
    box-shadow: inset 0 -1*token('border-width') 0 token('color-border-tab'); }
  display: flex; }

.tw-tabs__item {
  &:not(:last-child) {
    margin-right: $space-2; }

  > a,
  > button {
    @include theme((color: 'color-text-tab'));
    display: inline-block;
    font-size: $font-size-5;
    padding: $space-05 0;

    &:hover {
      @include theme {
        box-shadow: 0 -1*token('border-width') 0 token('color-border-tab-hover') inset; }
      text-decoration: none; }

    &:focus {
      @include theme {
        box-shadow: 0 -2px 0 token('color-border-tab-focus') inset, token('shadow-tab-focus'); }
      outline: none; }

    // The 'active' class is set by the Ember router. It
    // applies to the link that matches the current route.
    &.active {
      @include theme {
        color: token('color-text-tab-active');
        box-shadow: 0 -1*token('border-width') 0 token('color-border-tab-active') inset; } } } }

// Tabs stretch across their container and evenly
// distribute horizontally.
.tw-tabs--fullwidth {
  text-align: center;

  .tw-tabs__item {
    flex: 1 0 auto;
    margin: 0;

    a {
      display: block; } } }



