@import 'core.variables';

.tw-form__input-group {
  position: relative;
  flex-grow: 1; }

.tw-form__label {
  @include theme((color: 'color-text-label'));
  @include baseline($font-size-6, $lineheight: 1);
  display: inline-block;
  position: relative;
  font-weight: $font-weight-bold; }

.tw-form__label--optional {
  &::after {
    @include theme((color: 'color-text-label-optional'));
    padding-left: $space-05;
    font-weight: normal;
    content: '(optional)'; } }

// hint
.tw-form__hint {
  @include theme((color: 'color-text-hint'));
  @include baseline($font-size-7, $below: 0);
  display: block; }

// Error States
.tw-form__hint--error {
  display: none; }

.tw-form__group--error {
  .tw-form__label {
    @include theme((color: 'color-text-error')); }

  .tw-form__input,
  .tw-form__input:focus {
    @include theme {
      box-shadow: inset 0 0 0 token('border-width') token('color-border-input-error'); } }

  .tw-form__hint {
    display: none; }

  .tw-form__hint--error {
    @include theme((color: 'color-text-error'));
    display: block; } }



