// Opacity Values
$opac-1    : 0.05;
$opac-2    : 0.1;
$opac-3    : 0.15;
$opac-4    : 0.2;
$opac-5    : 0.25;
$opac-6    : 0.3;
$opac-7    : 0.4;
$opac-8    : 0.5;
$opac-9    : 0.6;
$opac-10   : 0.7;
$opac-11   : 0.75;
$opac-12   : 0.8;
$opac-13   : 0.85;
$opac-14   : 0.9;
$opac-15   : 0.95;

// Black Opacities
// These are shades of black determined by opacity values
$opac-b-1  : rgba($black, $opac-1);
$opac-b-2  : rgba($black, $opac-2);
$opac-b-4  : rgba($black, $opac-4);
$opac-b-5  : rgba($black, $opac-5);
$opac-b-7  : rgba($black, $opac-7);
$opac-b-8  : rgba($black, $opac-8);
$opac-b-9  : rgba($black, $opac-9);
$opac-b-10 : rgba($black, $opac-10);
$opac-b-12 : rgba($black, $opac-12);
$opac-b-13 : rgba($black, $opac-13);
$opac-b-14 : rgba($black, $opac-14);
$opac-b-15 : rgba($black, $opac-15);

// White Opacities
// These are shades of white determined by opacity values
$opac-w-1  : rgba($white, $opac-1);
$opac-w-2  : rgba($white, $opac-2);
$opac-w-4  : rgba($white, $opac-4);
$opac-w-5  : rgba($white, $opac-5);
$opac-w-7  : rgba($white, $opac-7);
$opac-w-8  : rgba($white, $opac-8);
$opac-w-9  : rgba($white, $opac-9);
$opac-w-10 : rgba($white, $opac-10);
$opac-w-12 : rgba($white, $opac-12);
$opac-w-13 : rgba($white, $opac-13);
$opac-w-14 : rgba($white, $opac-14);
$opac-w-15 : rgba($white, $opac-15);

// Twitch Purple Opacities
// These are shades of Twitch Purple determined by opacity values
$opac-p-1  : rgba($twitch-purple, $opac-1);
$opac-p-2  : rgba($twitch-purple, $opac-2);
$opac-p-4  : rgba($twitch-purple, $opac-4);
$opac-p-5  : rgba($twitch-purple, $opac-5);
$opac-p-7  : rgba($twitch-purple, $opac-7);
$opac-p-8  : rgba($twitch-purple, $opac-8);
$opac-p-9  : rgba($twitch-purple, $opac-9);
$opac-p-10 : rgba($twitch-purple, $opac-10);
$opac-p-12 : rgba($twitch-purple, $opac-12);
$opac-p-13 : rgba($twitch-purple, $opac-13);
$opac-p-14 : rgba($twitch-purple, $opac-14);
$opac-p-15 : rgba($twitch-purple, $opac-15);



