@import "core.variables";

.interactable {
  @include theme((color: color-text-link));
  display: block;
  width: 100%;
  text-align: left; // overide for default browser button styles that should be a in a reset
  text-decoration: none;

  &:hover,
  &--hover {
    @include theme((color: color-text-interactable-hover, background: color-background-interactable-hover));
    text-decoration: none; }
  &:active {
    @include theme {
      background: token(color-background-interactable-active);
      color: token(color-text-interactable-active); } }
  &:focus {
    @include theme((box-shadow: 'shadow-interactable-focus'));
    outline: 0; }
  &--selected {
    @include theme {
      background: token-get(color-background-interactable-selected);
      color: token-get(color-text-interactable-selected); } }
  &:disabled,
  &--disabled {
    @include theme {
      border-color: token-get('color-border-button-disabled');
      background: token-get('color-background-button-disabled');
      color: token-get('color-text-button-disabled'); }
    cursor: default;
    pointer-events: none; } }



