@import "core.variables";

.token-table-row {
  display: table-row;

  &__cell {
    @include theme {
      border-top: token('border-width') solid token('color-border'); }
    display: table-cell;
    padding: $space-1 $space-2;
    font-size: $font-size-4;
    vertical-align: middle;

    &:not(:first-child) {
      width: 32%; } } }


