@import "core.variables";
// Card Body Holds Title and Info
//  ------------------------------
.tw-card-body {
  position: relative;
  // see https://css-tricks.com/flexbox-truncated-text/
  flex-shrink: 1;
  min-width: 0; }

.tw-card-body--overlay {
  @include theme((color: color-text-overlay));
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  flex-direction: column;
  justify-content: flex-end;
  background: url('data:image/png;base64,') repeat-x bottom;
  background-size: auto 100%;
  overflow: hidden; }



