// Tower settings.
// ----------------------------------------------------------------------------
$grid-columns                 : 12;
$grid-gutter-width            : 1.5rem;
$grid-gutter-width-lg         : 2rem;
$grid-gutter-width-sm         : 1rem;
$grid-gutter-width-xs         : 0.5rem;
$grid-max-width               : 980px;


// Breakpoints.
// ----------------------------------------------------------------------------
$break-xs:   480px;
$break-sm:   768px;
$break-md:   1024px;
$break-lg:   1200px;
$break-xl:   1440px;
$break-xxl:  1600px;

// Map the breakpoints to keys.
$breakpoints: ('xs': $break-xs, 'sm': $break-sm, 'md': $break-md, 'lg': $break-lg, 'xl': $break-xl, 'xxl': $break-xxl);



