// Progress Bar
// ----------------------------------------------------------------------------
.progress-bar {
  @include theme((background: 'color-background-progress'));
  height: 1rem;
  width: 100%; }

.progress-bar__fill {
  @include theme((background: 'color-background-progress-status'));
  display: block;
  max-width: 100%;
  height: 100%; }

// @TODO: update use more explicit pinning syntax. This is an old class that
// came from video_watched.
.progress-bar-wrapper {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  pointer-events: none; }


// Size Modifiers
// ----------------------------------------------------------------------------
.progress-bar--xs {
  height: 0.3rem; }

.progress-bar--sm {
  height: 0.5rem; }


// State Modifiers
// ----------------------------------------------------------------------------
.progress-bar--error {
  @include theme((background: 'color-background-progress-status-error'));

  .progress-bar__fill {
    visibility: hidden; } }

.progress-bar--caution {
  @include theme((background: 'color-background-progress-status-caution'));

  .progress-bar__fill {
    visibility: hidden; } }

// Countdown
// ----------------------------------------------------------------------------
.progress-bar--countdown {
  @include theme((background: 'color-background-progress-countdown'));

  .progress-bar__fill {
    @include theme((background: 'color-background-progress-countdown-status'));
    width: 100%;
    animation: progress-bar-countdown linear both; } }


@keyframes progress-bar-countdown {
  100% {
    width: 0%; } }



