// Available Spacing Variables.
$space-0:   0;
$space-05:  0.5rem;
$space-1:   1rem;
$space-2:   2rem;
$space-3:   3rem;
$space-4:   4rem;
$space-5:   5rem;
$space-6:   6rem;
$space-7:   7rem;
$space-8:   8rem;
$space-9:   9rem;
$space-10:  10rem;

// Map the variables to classname-friendly keys.
$spacing-increments: ('0': $space-0, '05': $space-05, '1': $space-1, '2': $space-2, '3': $space-3, '4': $space-4, '5': $space-5, '6': $space-6, '7': $space-7, '8': $space-8, '9': $space-9, '10': $space-10);



